<template>
  <div class="dt-main">
    <div id='traf'></div>
  </div>
</template>
<script>
var map
export default {
  props: ['mapCenter', 'marketList'],
  data () {
    //这里存放数据
    return {

    };
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
  },
  //方法集合
  methods: {
    initMap () {
      //定义地图中心点坐标
      var center = new TMap.LatLng(this.mapCenter.lat, this.mapCenter.lng)
      //定义map变量，调用 TMap.Map() 构造函数创建地图
      map = new TMap.Map(document.getElementById('traf'), {
        center: center,//设置地图中心点坐标
        zoom: 14.8,   //设置地图缩放级别
        viewMode: '2D',
        baseMap: [
          { type: 'vector' }, //设置矢量底图
          { type: 'traffic' } //设置路况底图
        ],

      });

      var marker = new TMap.MultiMarker({
        id: 'shexiangtou',
        map: map,
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          "myStyle": new TMap.MarkerStyle({
            "width": 35,  // 点标记样式宽度（像素）
            "height": 35, // 点标记样式高度（像素）
            "src": 'https://tse2-mm.cn.bing.net/th/id/OIP.sDurzp3pCBK8UcIzoZg0SgHaHa?pid=Api&rs=1',  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: '50%', y: '50%' }
          })
        },
        //点标记数据数组
        geometries: this.getGeometries()
      })
      //监听回调函数（非匿名函数）
      var clickHandler = function (evt) {
        console.log("点击", evt.geometry.properties.title)
      }
      //监听marker点击事件
      marker.on("click", clickHandler)

    },
    getGeometries () {
      let list = []
      for (let index in this.marketList) {
        let market = this.marketList[index]
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyle',  //指定样式id
          "position": new TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.id
          }
        })
      }
      return list
    },

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../styles/dimens";
.dt-main {
  position: relative;
  width: 100%;
  height: 88.5vh;
  #traf {
    width: 100%;
    height: 100%;
  }
}
</style>
