<!--交通状态监测-->
<template>
  <van-row class="dt-home">
    <!-- 顶部框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">交通状态监测</p>
      </van-col>
    </van-row>
    <!--地图-->
    <van-row class="dt-content">
      <traS :mapCenter="mapCenter"
            :marketList="marketList"></traS>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import traS from "@/components/map/trafficStateMonitoring";
export default {
  //import引入的组件需要注入到对象中才能使用

  components: {
    traS
  },
  data () {
    //这里存放数据
    return {
      //地图中心点
      mapCenter: { lat: 32.332435, lng: 119.088235 },
      //标记点经纬度
      marketList: []
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    Home () {
      this.$router.push({ name: 'Home' })
    },
    back () {
      this.$store.commit("active", 0)
      javascript: history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../../styles/dimens";
$noticeBarHeight: px2rem(50);
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: "SourceHanSansCN-Medium";
  color: #ecedf2;
}
/deep/ .tmap-scale-control,
/deep/ .logo-text,
/deep/ .dt-main img,
/deep/ .rotate-top,
/deep/ .rotate-right,
/deep/ .rotate-bottom,
/deep/ .rotate-left,
/deep/ .rotate-circle,
/deep/ .tmap-zoom-control {
  display: none;
}
